@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;700&display=swap);
.container-icon{display:flex;justify-content:space-between;margin:2rem 0}.container-icon button{font-size:2.6rem;border:none;cursor:pointer;width:4.2rem;height:4.2rem;border-radius:10rem;transition:all .12s ease-in-out;color:#e4c073;outline:none;background:transparent}.container-icon button:hover{background:#ece5d7}.container-icon button:active{opacity:.7}.container-icon .button-primary{color:#4a8bcd}.container-icon .button-primary:hover{color:#fff;background:#4a8bcd}.container-icon .tooltip{font-family:"Montserrat", sans-serif;font-size:1.2em;font-weight:400}

table{margin-top:2.4rem;width:100%;font-family:"Montserrat", sans-serif;border-collapse:collapse;color:#666}table thead{background:#fff;box-shadow:RGBA(144, 138, 125, 0.2) 2px 2px 16px -7px}table thead th{text-align:left;padding:1rem}table thead th:last-of-type{width:3rem;white-space:nowrap;border-radius:0 5px 5px 0}table thead th:first-of-type{border-radius:5px 0 0 5px}table tbody tr{font-weight:400;font-size:.95em;border-bottom:solid 1px #ccc;transition:all 80ms ease-in-out}table tbody tr:hover{background:rgba(255,255,255,0.4)}table tbody tr td{padding:.8rem}table tbody tr td:last-of-type{border-radius:0 5px 5px 0}table tbody tr td:first-of-type{border-radius:5px 0 0 5px}table button{width:2rem;height:2rem;border-radius:3rem;border:none;background:transparent;cursor:pointer;outline:none;transition:all 80ms ease-in-out}table button:hover{background:#ece5d7}table button:active{opacity:.6}table .icon{font-size:1.2rem;color:#666}

nav{font-family:"Montserrat", sans-serif}nav :first-of-type{margin-right:1.4rem}nav a{font-weight:500;height:2.5rem;display:inline-block;line-height:2.6;padding:0 1.4rem;border-radius:10rem;text-decoration:none;font-size:1em;border:solid 1px #dfba6b;color:#4480bd;transition:all 90ms ease-in-out}nav a:hover{background:#4480bd;border-color:#4480bd;color:#fff;box-shadow:RGBA(68, 128, 189, 0.9) 2px 2px 20px -7px}nav a:active{transform:scale(0.98);box-shadow:none;opacity:0.4}nav .icon{margin-right:0.7rem}.active{background:#4480bd;border-color:#4480bd;color:#fff;box-shadow:RGBA(68, 128, 189, 0.9) 2px 2px 20px -7px}@media only screen and (max-width: 996px){nav{display:flex;justify-content:center}}

h1{font-family:'Montserrat', sans-serif;color:#876211;font-size:1.9em;font-weight:300;margin:3rem auto;white-space:nowrap;text-align:center}h1 span{display:block;font-weight:700;font-size:3.5rem;color:#4a8bcd}img{display:block;margin:0 auto;width:100%}@media only screen and (max-width: 996px){h1{margin:1.3rem auto}img{width:12rem;height:12rem;border-radius:30rem;margin-bottom:-6rem;position:relative;z-index:9;border:solid 3px #f9f3e6}}

*{margin:0;padding:0}body,html,#root{height:100%;overflow:hidden}#root{display:flex;background:#f9f3e6}#root .section-sidebar{background:#f2cf81;width:26rem}#root .section-content{padding:3rem 4rem;flex-grow:2;overflow-y:auto}@media only screen and (max-width: 1020px){#root .section-sidebar{width:24rem}}@media only screen and (max-width: 996px){body,html,#root{font-size:13px;overflow:auto}#root{flex-direction:column}#root .section-content{padding:3rem;overflow-y:visible;overflow-y:initial}#root .section-sidebar{width:100%;margin-bottom:5rem}}

